import React, { Component} from 'react';
let classNames = require('classnames');

export class LinkMessage extends Component {
    render() {
        let class_name = classNames('message_text message', {
            'message__incoming message_content__incoming': this.props.is_incoming,
            'message__outcoming message_content__outcoming': this.props.is_incoming,
            'last_thing': this.props.is_last
        });

        return (
            <div className={class_name}>
                <div><a className="message_link" href={this.props.link}>{this.props.text}</a></div>
            </div>
        )
    }
}